"use client";
import React, { useState } from "react";

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <nav className="bg-transparent w-full overflow-hidden">
      <div className="container mx-auto px-4 py-2 flex items-center justify-between">
        {/* Logo */}
        <div className="text-4xl font-bold text-blue-700">SkillShare</div>

        {/* Desktop Menu */}
        <ul className="hidden md:flex space-x-12 text-blue-700 font-medium">
          <li>
            <a href="#" className="hover:text-blue-500">
              Home
            </a>
          </li>
          <li>
            <a href="#" className="hover:text-blue-500">
              About
            </a>
          </li>
          <li>
            <a href="#" className="hover:text-blue-500">
              Services
            </a>
          </li>
          <li>
            <a href="#" className="hover:text-blue-500">
              Resources
            </a>
          </li>
          <li>
            <a href="#" className="hover:text-blue-500">
              Contact
            </a>
          </li>
        </ul>

        {/* Buttons */}
        <div className="hidden md:flex space-x-4">
          <button className="px-4 py-2 border text-blue-700 rounded-3xl hover:bg-blue-300 hover:text-white">
            Login
          </button>
          <button className="px-4 py-2 bg-blue-300 text-white rounded-3xl hover:bg-blue-400">
            Signup
          </button>
        </div>

        {/* Mobile Menu Toggle */}
        <button
          className="md:hidden text-blue-700 focus:outline-none"
          onClick={() => setIsOpen(!isOpen)}
        >
          {isOpen ? (
            <span className="text-2xl">✖️</span>
          ) : (
            <span className="text-2xl">☰</span>
          )}
        </button>
      </div>

      {/* Mobile Menu */}
      {isOpen && (
        <div className="md:hidden bg-blue-100 w-full absolute left-0 z-10">
          <ul className="flex flex-col items-center space-y-4 py-4 text-blue-700 font-medium">
            <li>
              <a href="#" className="hover:text-blue-500">
                Home
              </a>
            </li>
            <li>
              <a href="#" className="hover:text-blue-500">
                About
              </a>
            </li>
            <li>
              <a href="#" className="hover:text-blue-500">
                Services
              </a>
            </li>
            <li>
              <a href="#" className="hover:text-blue-500">
                Resources
              </a>
            </li>
            <li>
              <a href="#" className="hover:text-blue-500">
                Contact
              </a>
            </li>
            <div className="space-y-2 w-3/4">
              <button className="w-full px-4 py-2 border border-blue-700 text-blue-700 rounded-lg hover:bg-blue-700 hover:text-white">
                Login
              </button>
              <button className="w-full px-4 py-2 bg-blue-700 text-white rounded-lg hover:bg-blue-800">
                Signup
              </button>
            </div>
          </ul>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
